"use client";

import { GlassCard } from "@/components/layout/glass-card";
import type { LightControlProps } from "./widget-types";
import { cn } from "@/lib/utils";

export function LightControlWidget({
  title = "Light Control",
  value = 75,
  size = "md",
  className,
}: LightControlProps & { className?: string }) {
  const pct = Math.min(100, Math.max(0, value ?? 0));
  return (
    <GlassCard
      className={cn(
        "p-4",
        size === "lg" && "p-5",
        size === "sm" && "p-3",
        className
      )}
    >
      <h3 className="text-card-title font-medium mb-3">{title}</h3>
      <div className="flex items-center gap-4">
        <input
          type="range"
          min={0}
          max={100}
          value={pct}
          readOnly
          className="flex-1 h-2 rounded-full appearance-none bg-gray-200 dark:bg-white/10 accent-accent-yellow dark:accent-accent-green"
          style={{
            background: `linear-gradient(to right, var(--tw-gradient-from), var(--tw-gradient-to))`,
          }}
        />
        <span className="text-lg font-bold text-accent-yellow dark:text-accent-green w-12 text-right">
          {pct}%
        </span>
      </div>
    </GlassCard>
  );
}
