"use client";

import { useParams, useRouter } from "next/navigation";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { AppShell } from "@/components/layout/app-shell";
import { HeroSection } from "@/components/layout/hero-section";
import { useState, useEffect } from "react";
import ReactGridLayout from "react-grid-layout";
import "react-grid-layout/css/styles.css";
import "react-resizable/css/styles.css";

type LayoutItem = ReactGridLayout.Layout;
type Layout = LayoutItem[];
import {
  EnergyUsageWidget,
  LightControlWidget,
  WifiWidget,
  SolarChargeWidget,
  ClimateWidget,
  LightingBrightnessWidget,
} from "@/components/widgets";
import type { WidgetConfig } from "@/stores/onboarding-store";
import { useEntityStateStore } from "@/stores/entity-state-store";
import { useEntityStatePolling } from "@/hooks/use-entity-state";
import { OfflinePill } from "@/components/offline-pill";
import { cn } from "@/lib/utils";

type DashboardData = {
  id: string;
  name: string;
  theme: string;
  layout: string | null;
  widgets: string | null;
  background: string | null;
};

function parseLayout(layout: string | null): Layout {
  if (!layout) return [];
  try {
    const arr = JSON.parse(layout) as LayoutItem[];
    return Array.isArray(arr) ? arr : [];
  } catch {
    return [];
  }
}

function parseWidgets(widgets: string | null): WidgetConfig[] {
  if (!widgets) return [];
  try {
    const arr = JSON.parse(widgets) as WidgetConfig[];
    return Array.isArray(arr) ? arr : [];
  } catch {
    return [];
  }
}

function WidgetByType({
  type,
  title,
  entity_id,
  size,
}: {
  type: string;
  title: string;
  entity_id: string;
  size?: string;
}) {
  const sizeProp = (size as "sm" | "md" | "lg") ?? "md";
  const live = useEntityStateStore((s) => s.getState(entity_id));
  const num = live?.state != null ? Number(live.state) : undefined;
  const onOff = live?.state === "on" ? "on" : "off";

  switch (type) {
    case "energy_usage":
      return <EnergyUsageWidget title={title} entity_id={entity_id} size={sizeProp} value={num} />;
    case "light_control":
      return <LightControlWidget title={title} entity_id={entity_id} size={sizeProp} value={num} />;
    case "wifi":
      return <WifiWidget title={title} entity_id={entity_id} size={sizeProp} value={onOff} />;
    case "solar_charge":
      return <SolarChargeWidget title={title} entity_id={entity_id} size={sizeProp} value={num} />;
    case "climate":
      return <ClimateWidget title={title} entity_id={entity_id} size={sizeProp} value={num} />;
    case "lighting_brightness":
      return <LightingBrightnessWidget title={title} entity_id={entity_id} size={sizeProp} value={num} />;
    default:
      return (
        <div className="rounded-card border border-dashed p-4 text-sm text-gray-500">
          {title || type}
        </div>
      );
  }
}

export default function DashboardEditPage() {
  const params = useParams();
  const router = useRouter();
  const queryClient = useQueryClient();
  const id = params?.id as string;
  const [editMode, setEditMode] = useState(true);
  const [layout, setLayout] = useState<Layout>([]);
  const [widgets, setWidgets] = useState<WidgetConfig[]>([]);
  const [initialized, setInitialized] = useState(false);
  const [uploadingBg, setUploadingBg] = useState(false);

  const { data, isLoading, error } = useQuery<DashboardData>({
    queryKey: ["dashboard", id],
    queryFn: async () => {
      const res = await fetch(`/api/dashboards/${id}`);
      if (!res.ok) throw new Error("Failed to load");
      return res.json();
    },
    enabled: !!id,
  });

  useEntityStatePolling();

  useEffect(() => {
    if (!data || initialized) return;
    const w = parseWidgets(data.widgets);
    setWidgets(w);
    const parsed = parseLayout(data.layout);
    if (parsed.length > 0) {
      setLayout(parsed);
    } else if (w.length > 0) {
      setLayout(
        w.map((widget, i) => ({
          i: widget.id,
          x: (i % 3) * 4,
          y: Math.floor(i / 3) * 2,
          w: 4,
          h: 2,
        }))
      );
    }
    setInitialized(true);
  }, [data, initialized]);

  const saveMutation = useMutation({
    mutationFn: async (payload: { layout: Layout; widgets: WidgetConfig[] }) => {
      const res = await fetch(`/api/dashboards/${id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          layout: JSON.stringify(payload.layout),
          widgets: JSON.stringify(payload.widgets),
        }),
      });
      if (!res.ok) throw new Error("Save failed");
      return res.json();
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["dashboard", id] });
    },
  });

  const onLayoutChange = (newLayout: Layout) => {
    setLayout(newLayout);
  };

  const layoutMap = new Map(layout.map((item) => [item.i, item]));

  const handleSave = () => {
    saveMutation.mutate({ layout, widgets });
  };

  const handleBackgroundUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file || !id) return;
    setUploadingBg(true);
    e.target.value = "";
    try {
      const formData = new FormData();
      formData.set("file", file);
      const res = await fetch("/api/upload", { method: "POST", body: formData });
      const json = await res.json();
      if (!res.ok) throw new Error(json.error || "Upload mislukt");
      await fetch(`/api/dashboards/${id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ background: json.url }),
      });
      queryClient.invalidateQueries({ queryKey: ["dashboard", id] });
    } catch {
      // show error in UI if you add a toast
    } finally {
      setUploadingBg(false);
    }
  };

  const removeBackground = async () => {
    if (!id) return;
    try {
      await fetch(`/api/dashboards/${id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ background: null }),
      });
      queryClient.invalidateQueries({ queryKey: ["dashboard", id] });
    } finally {
      setUploadingBg(false);
    }
  };

  const handleDeleteDashboard = async () => {
    if (!id || !data?.name) return;
    if (!confirm(`Dashboard "${data.name}" definitief verwijderen?`)) return;
    const res = await fetch(`/api/dashboards/${id}`, { method: "DELETE" });
    if (!res.ok) return;
    queryClient.invalidateQueries({ queryKey: ["dashboards"] });
    router.push("/dashboards");
  };

  const bgImage =
    data?.background ||
    (typeof document !== "undefined" && document.documentElement.classList.contains("dark")
      ? "dark"
      : "light");

  if (!id) {
    return (
      <AppShell activeTab="/dashboards">
        <p className="text-sm text-gray-500">Invalid dashboard id.</p>
      </AppShell>
    );
  }

  if (isLoading || error) {
    return (
      <AppShell activeTab="/dashboards">
        <p className="text-sm text-gray-500">
          {error ? "Failed to load dashboard." : "Loading…"}
        </p>
      </AppShell>
    );
  }

  return (
    <AppShell activeTab="/dashboards">
      <div className="space-y-6">
        <div className="flex items-center justify-between gap-4">
          <h2 className="text-xl font-semibold flex items-center gap-2">
            {data?.name ?? "Dashboard"}
            <OfflinePill />
          </h2>
          <div className="flex items-center gap-2">
            <button
              type="button"
              onClick={() => setEditMode((e) => !e)}
              className={cn(
                "rounded-full px-3 py-1.5 text-sm font-medium",
                editMode
                  ? "bg-accent-yellow dark:bg-accent-green text-gray-900"
                  : "bg-gray-200 dark:bg-white/10"
              )}
            >
              {editMode ? "Edit" : "Preview"}
            </button>
            {editMode && (
              <>
                <button
                  type="button"
                  onClick={handleSave}
                  disabled={saveMutation.isPending}
                  className="rounded-full bg-accent-purple dark:bg-accent-green px-4 py-2 text-sm font-medium text-white dark:text-gray-900"
                >
                  Save
                </button>
                <label className="rounded-full bg-gray-200 dark:bg-white/10 px-4 py-2 text-sm font-medium cursor-pointer hover:bg-gray-300 dark:hover:bg-white/20">
                  {uploadingBg ? "Bezig…" : "Hero-achtergrond uploaden"}
                  <input
                    type="file"
                    accept="image/jpeg,image/png,image/webp,image/gif"
                    className="sr-only"
                    onChange={handleBackgroundUpload}
                    disabled={uploadingBg}
                  />
                </label>
                {data?.background && (
                  <button
                    type="button"
                    onClick={removeBackground}
                    className="text-sm text-gray-500 hover:text-gray-700 dark:hover:text-gray-300"
                  >
                    Achtergrond verwijderen
                  </button>
                )}
              </>
            )}
            <button
              type="button"
              onClick={handleDeleteDashboard}
              className="text-sm text-red-500 hover:text-red-600 dark:text-red-400"
            >
              Dashboard verwijderen
            </button>
          </div>
        </div>

        <HeroSection
          title={data?.name ?? "Dashboard"}
          subtitle="Your Home Assistant dashboard"
          backgroundImage={bgImage}
        />

        <div className="rounded-card overflow-hidden">
          <ReactGridLayout
            className="layout"
            layout={layout}
            onLayoutChange={onLayoutChange}
            cols={12}
            rowHeight={80}
            width={1200}
            isDraggable={editMode}
            isResizable={editMode}
            draggableHandle={editMode ? undefined : ".no-drag"}
          >
            {widgets.map((w) => {
              const item = layoutMap.get(w.id);
              if (!item) return null;
              return (
                <div key={w.id} className={cn(editMode && "no-drag")}>
                  <WidgetByType
                    type={w.type}
                    title={w.title}
                    entity_id={w.entity_id}
                    size={w.size}
                  />
                </div>
              );
            })}
          </ReactGridLayout>
        </div>
      </div>
    </AppShell>
  );
}
